<template>
    <div class="message">
        <navigate title="我的消息" position="absolute"/>
        <!--<mu-tabs :value.sync="active" class="tabs border-1px-b" color="#2D8FCD"-->
        <!--text-color="#37373B"-->
        <!--indicator-color="#288DCD"-->
        <!--inverse-->
        <!--center>-->
        <!--<mu-tab style="margin: 0 20px" value="notice" to="/about/message?target=notice" replace>通知</mu-tab>-->
        <!--<mu-tab style="margin: 0 20px" value="news" to="/about/message?target=news" replace>消息</mu-tab>-->
        <!--</mu-tabs>-->
        <div class="scroll-wrapper" ref="scrollWrapper">
            <mu-load-more v-if="list.length" :refreshing="refresh" :loading="loading"
                          @refresh="onRefresh"
                          @load="onLoad">
                <template v-for="(item, index) in list">
                    <mu-ripple class="list-item border-1px-b" @click="handlerMessageItem(item)">
                        <div class="action">
                            <mu-avatar color="#2196f3" size="30">
                                <img style="padding: 5px" src="../../../../static/icon/news_notice.png">
                            </mu-avatar>
                        </div>
                        <div class="info">
                            <div class="name no-wrap">{{item.title}}</div>
                            <p class="no-wrap">{{item.content}}</p>
                        </div>
                        <div class="time">
                            {{item.createTime | formatPast}}
                        </div>
                    </mu-ripple>
                </template>
            </mu-load-more>
            <no-success :text="noSuccessText" v-else/>
        </div>
        <transition name="fade-left">
            <router-view class="views"/>
        </transition>
    </div>
</template>

<script>
    import Navigate from '../../../base/navigate/navigate'
    import FormatTime from '../../../common/utils/time'
    import {queryIMessage, queryAllNotice} from "../../../api/personal"
    import NoSuccess from "../../../components/no-success/no-success"

    export default {
        name: "message",
        data() {
            return {
                active: this.$route.query.target,
                refresh: false,
                loading: false,
                list: [],
                defaultAvatar: 'static/image/navigation-item-1.png',
                params: {
                    pageNumber: 0,
                    pageSize: 20
                }
            }
        },
        activated() {
            if (this.active === 'notice') {
                this.queryAllNotice()
            } else {
                this.queryIMessage()
            }
        },
        computed: {
            noSuccessText() {
                return this.active === 'notice' ? '您暂时还没有收到任何通知' : '您暂时还没有收到任何消息'
            }
        },
        methods: {
            handlerMessageItem(item) {
                this.$router.push(`/about/message/${this.$route.query.target}/${item.id}`)
            },
            // 发送请求
            queryIMessage() {
                this.params.pageNumber = 0

                queryIMessage(this.params).then(res => {
                    this.list = res
                    this.refresh = false
                    this.loading = false
                })
            },
            queryAllNotice() {
                this.params.pageNumber = 0

                queryAllNotice(this.params).then(res => {
                    this.list = res.content
                    this.refresh = false
                    this.loading = false
                })
            },
            // 下拉刷新
            onRefresh() {
                this.refresh = true
                this.$refs.scrollWrapper.scrollTop = 0

                if (this.active === 'notice') {
                    this.queryAllNotice()
                } else {
                    this.queryIMessage()
                }
            },
            // 上拉加载
            onLoad() {
                this.loading = true
                this.params.pageNumber++

                if (this.active === 'notice') {
                    queryAllNotice(this.params).then(res => {
                        this.list.push(...res)
                        this.loading = false
                    })
                } else {
                    queryIMessage(this.params).then(res => {
                        this.list.push(...res)
                        this.loading = false
                    })
                }
            }
        },
        filters: {
            formatPast: FormatTime.formatPast
        },
        watch: {
            active() {
                if (this.active === 'notice') {
                    this.queryAllNotice()
                } else {
                    this.queryIMessage()
                }
            }
        },
        components: {
            NoSuccess,
            Navigate
        }
    }
</script>

<style lang="stylus">
    @import "../../../common/stylus/variable.styl"

    .message
        padding-top 46px
        background-color #fff
        .tabs
            position absolute
            top 46px
            width 100%
        .scroll-wrapper
            width 100%
            height 100%
            overflow-x hidden
            overflow-y auto
        .list-item
            position relative
            display flex
            align-items center
            padding 0 15px
            height 60px
            .action
                margin-right 10px
            .info
                margin-right 10px
                flex 1
                color #C4C4C4
                font-size $font-size-ms
                overflow hidden
                > *
                    height 1.5em
                    line-height 1.5em
                .name
                    font-size $font-size-ls
                    color #3D3D3D
            .time
                align-self flex-start
                margin-top 15px
                font-size $font-size-ms
</style>



